export default {
	data() {
		return {
			// 总收益
			all:'0',
			// 前日收益
			lasmo:'0',
			// 昨日收益
			yester:'0',
			// 可提现金额
			wimoney:'0',
			// 提现模态框显示隐藏
			showMonmodal:false,
			// 有无提现记录
			haslist: 1,
			// token
			token:'',
			// 要体现的金额
			form:{
				cashMoney:''
			}
		};
	},
	onLoad() {
		this.getToken()
		// 调用获取收益信息的函数
		this.getWithdraw()
	},
	methods:{
		// 获取token
		getToken(){
			this.token = uni.getStorageSync('token');
		},
		getWithdraw(){
			// 获取收益信息，更新数据，绑定数据
			const that = this
			uni.request({
				url:'http://www.vzoyo.com/api/user/cashShow',
				method:'POST',
				header: { token: this.token },
				success(res) {
					console.log(res)
					that.all = res.data.data.total_money
					that.lasmo = res.data.data.before_yester_money
					that.yester = res.data.data.yester_money
					that.wimoney = res.data.data.able_money
				}
			})
		},
		getMoney(){
			this.showMonmodal = true
		},
		closeModal() {
			this.showMonmodal = false
		},
		bindBtn(type) {
			this.showMonmodal = false
		},
		// 关于模态框的函数
		withdraw(){
			const that = this
			uni.request({
				url:'http://www.vzoyo.com/api/user/applyWithdraw',
				method:'POST',
				header: { 
					token: this.token,
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				data:{
					money:that.form.cashMoney
				},
				success(res) {
					console.log(res)
				}
			})
		}
		
	}
}
